
@import "../_component.scss";



@mixin bg-cloud($index){
	div {
		position: absolute;
		left: 0;
		top: 0;
		overflow: hidden;
		width: 100%;
		height: 100%;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			min-width: 1200px;
			height: 100%;
			background-repeat: no-repeat;
			animation-iteration-count: infinite;
			animation-direction: normal;
			animation-timing-function: linear;
			animation-fill-mode: forwards;
			animation-name: cloud;
			transform: translateX(100%);
		}
		&.bg-cloud-1 {
			&:before {
				animation-duration: 70s;
				animation-delay: -40s;
				background-image: url('../images/visual/cloud-1.png');
				background-position: 0% 50%;
			}
			&:after {
				animation-duration: 65s;
				animation-delay: 10s;
				background-image: url('../images/visual/cloud-2.png');
				background-position: 50% 50%;
			}
		}
		&.bg-cloud-2 {
			&:before {
				animation-duration: 75s;
				animation-delay: -20s;
				background-image: url('../images/visual/cloud-3.png');
				background-position: 25% 50%;
			}
			&:after {
				animation-duration: 55s;
				animation-delay: 0s;
				background-image: url('../images/visual/cloud-4.png');
				background-position: 75% 50%;
			}
		}
	}
}


.bg-sub{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-position: 50% 0;
		background-repeat: no-repeat;
	}
}
