.section-sub-visual{
	position: relative;
	height: 203px;
	text-align: center;
	background-color: #f2f2f2;
	& > .inner{
		position: relative;
		max-width: 1903px;
		margin: 0 auto;
	}
	h2{
		padding-top: 65px;
		font-weight: 700;
		font-size: 42px;
		color: #000;
	}
	small{
		display: block;
		padding-top: 15px;
		font-size: 14px;
		color: #333;
	}
	.location{
		position: absolute;
		top: 20px;
		left: 30px;
		span{
			font-size: 12px;
			color: #333;
			a{
				position: relative;
				padding-right: 15px;
				text-decoration: underline;
				color: #0061a0;
				&:before,
				&:after{
					content: '';
					position: absolute;
					right: 0;
					display: inline-block;
					width: 7px;
					height: 1px;
					background-color: #444;
				}
				&:before{
					top: 7px;
					transform: rotate(25deg);
				}
				&:after{
					top: 10px;
					transform: rotate(-25deg);
				}
			}
		}
	}
}

@keyframes cloud {
	0% {
		transform: translateX(100%);
	}
	100% {
		transform: translateX(-100%);
	}
}
